


import React from 'react'



const EncartEventLarge = ({image, jour,moisAnnee,nomBar,nomEvenement,lienFb}) => (



<div className="col  col-lg-12">
    <div className="single-event-area mb-5">
        <div className="event-thumbnail">
            <img src={image} alt="" />
        </div>
        <div className="event-text">
            <div className="dateLeft">
                <span className="dateLeft-dateJour">{jour}</span><br/>
                <span className="dateLeft-dateJour2">{moisAnnee}</span><br/>
                <span className="dateLeft-dateJour2">{nomBar}</span>
            </div>
            <h4>{nomEvenement}</h4>

            <a  target="_blank"
                rel="noopener noreferrer"
                href={lienFb}
                className={lienFb == '' ? "btn see-more-btn Eventdisabled" : "btn see-more-btn " }
            >{lienFb == '' ? 'COMING SOON' : 'SEE EVENT' }</a>
        </div>
    </div>
</div>

)

export default EncartEventLarge
